.ExperienceSection {
    padding-top: 80px;
    padding-bottom: 40px;
    padding-left: 50px;
    margin: auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    background-color: var(--lightblue);
}

    .left {
        flex-basis: 20%;
    }
    
    .right {
        flex-basis: 80%;
        justify-content: center;
        padding-left: 90px;
        padding-right: 20px;
    }


.Blue {
    border-color: #082046;
}

.experience-listings {
    margin-left: 30px;
    font-size: 18px;
    margin-bottom: 40px;
}

.experience-title {
    font-weight: bold;
    font-size: 25px;
}

.experience-subtitle {
    margin-top: 10px;
    font-size: 20px;
}

@media only screen and (max-width: 800px) {
    .ExperienceSection {
        padding-top: 10px;
        padding-bottom: 10px;
        padding-left: 0px;
        margin: 0px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        background-color: var(--lightblue);
    }

    .left {
        padding-left: 8px;
        padding-right: 8px;
        flex-basis: 100%;
        margin-bottom: 10px;
    }
    
    .right {
        flex-basis: 100%;
        justify-content: center;
        padding-left: 8px;
        padding-right: 8px;
    }

    .experience-listings {
        margin-left: 0px;
        font-size: 10px;
        margin-bottom: 40px;
    }

    .experience-subtitle {
        margin-top: 3px;
        font-size: 16px;
    }

    .experience-title {
        font-weight: bold;
        font-size: 17px;
    }
}